          <div class="modal" v-if="dataStore.politicas">
            <KoTermsConditions :dataStore="dataStore" />
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import KoNewsLetter from '@/components/template15/Ko15-Newsletter.vue'
import KoSocialNet from '@/components/template11/ko-socialnet'
import KoTermsConditions from '@/components/footers/ko-TermsAndConditions'
import idCloudinaryBanner from '@/mixins/idCloudinary'
export default {
  name: 'Ko-Footer-10',
  components: {
    KoNewsLetter,
    KoSocialNet,
    KoTermsConditions,
  },
  mixins: [idCloudinaryBanner],
  props: {
    dataStore: Object,
    settingByTemplate15: Array,
  },
  mounted() {
    if (this.settingByTemplate15 && this.settingByTemplate15[0].footer) {
      this.setLogo()
    }
    var acc = document.getElementsByClassName('accordion')
    var i
    for (i = 0; i < acc.length; i++) {
      acc[i].addEventListener('click', function () {
        this.classList.toggle('active')
        var panel = this.nextElementSibling
        if (panel.style.maxHeight) {
          panel.style.maxHeight = null
        } else {
          panel.style.maxHeight = panel.scrollHeight + 10 + 'px'
        }
      })
    }
  },
  data() {
    return {
      secciones: [
        {
          name: 'header_inicio',
          path: '/',
        },
        {
          name: 'header_productos',
          path: '/productos',
        },
        {
          name: 'header_contacto',
          path: '/contacto',
        },
        {
          name: 'footer_micompra',
          path: '/micompra',
        },
        {
          name: 'header_blog',
          href: '/blog',
        },
      ],
      logo: null,
    }
  },
  computed: {
    listArticulos() {
      return this.$store.state.listArticulos.length
    },
    showModal() {
      return this.$store.state.modalpolitics05
    },
  },
  methods: {
    OpenModalPolitics() {
      this.$store.state.modalpolitics05 = true
    },
    setLogo() {
      let color = getComputedStyle(this.$refs.background).getPropertyValue(
        '--background_color_1'
      )
      let colorArray = color.split(',')
      let colorInt = parseInt(colorArray[2])
      if (colorInt > 50) {
        this.logo = true
      } else {
        this.logo = false
      }
    },
  },
  watch: {
    settingByTemplate15() {
      if (this.settingByTemplate15 && this.settingByTemplate15[0].footer) {
        let color = this.settingByTemplate15[0].footer['--background_color_1']
        let colorArray = color.split(',')
        let colorInt = parseInt(colorArray[2])
        if (colorInt > 50) {
          this.logo = true
        } else {
          this.logo = false
        }
      }
    },
  },
}
</script>

<style scoped>
.modal {
  padding-top: 200px;
  background-color: rgb(0, 0, 0);
  background-color: rgba(0, 0, 0, 0.4);
  @apply w-full h-full fixed z-10 left-0 top-0 overflow-auto;
}
.empty {
  background-color: var(--color_border);
  @apply w-full h-1 flex flex-col justify-center items-center;
}
@screen sm {
  .content-items-sm {
    @apply w-full flex flex-col justify-center items-center;
  }
  .content-store-sm {
    @apply w-9/0 flex flex-col justify-center items-start;
  }
  .content-direction,
  .content-number,
  .content-email {
    @apply w-full flex flex-col justify-center items-start;
  }
  .info-store-sm,
  .info-legal-sm,
  .info-networks {
    @apply w-full flex flex-col justify-center items-start;
  }

  .btn {
    color: var(--color_title);
    font-size: 18px;
    font-family: var(--font-style-1) !important;
    @apply pb-10;
  }
  .txt {
    color: var(--color_text);
    font-size: 14px;
    font-family: var(--font-style-1) !important;
  }
  .btns:hover {
    color: var(--hover_text);
  }
  .btn-legal {
    @apply w-full flex flex-col justify-center items-start text-left;
  }
  .content-items-lg {
    @apply hidden;
  }
  .madebyKomercia {
    @apply w-full flex flex-col justify-center items-center py-20;
  }
  .txt-devBy {
    font-size: 14px;
    color: var(--color_text);
  }
  .logo2 {
    width: 100px;
    opacity: 0.5;
  }
}
@screen lg {
  .content-items-sm {
    @apply hidden;
  }
  .content-items-lg {
    @apply w-9/0 grid grid-cols-3 gap-4 justify-center items-start;
  }
  .img-logo {
    max-width: var(--with_logo);
    @apply w-full object-contain;
  }
  .info-btn-footer {
    @apply w-full flex flex-col justify-center items-start;
  }
  .btns {
    color: var(--color_text);
    font-size: 14px;
    font-family: var(--font-style-1) !important;
  }
  .btns:hover {
    color: var(--hover_text);
  }
}
@media (min-width: 1200px) {
  .content-items-lg {
    @apply w-full max-w-7xl;
  }
}
</style>
